import React from 'react';
import style from "./Post.module.css";

const Post = (props) => {
    return (
    <div className={style.item}>
      <img src="data:image/png;base64," />
      { props.message }
      <div>
        <span>Like {props.likeCount}</span>
      </div>
    </div>
  )
};

export default Post;